<style scoped>
.question-box {
    text-align: left;
}
.select-item {
    min-height: 60px;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
}
.select-item:hover {
    background-color: rgba(230, 227, 227, 0.656);
}

.btn-box {
    text-align: center;
    margin-top: 20px;
}
.question-card {
    max-width: 100%;
}

.selected {
    background-color: #2db7f5;
}

.correct {
    background-color: rgb(173, 219, 80);
}

.wrong {
    background-color: rgb(252, 78, 78);
}

.selected:hover {
    background-color: #2db7f5;
}

.correct:hover {
    background-color: rgb(173, 219, 80);
}

.wrong:hover {
    background-color: rgb(252, 78, 78);
}

.img-box {
    text-align: center;
}

.question-img {
    width: 300px;
    height: 200px;
}

.question-title {
    font-size: 14px;
    font-weight: 600;
}
</style>
<template>
 <div class="question-box">
   <Card class="question-card">
        <Spin size="large" fix v-if="spinShow">加载中...</Spin>
        <p slot="title">
            <Icon size="18" type="md-albums" />
            第{{recentIndex}}题
        </p>
        <a href="#" slot="extra" @click.prevent="resetQuestion">
            重置本题
        </a>
        <div class="question-content">
          <Row>
            <Col class="question-title" span="24">{{questionTitle}}</Col>
            <Divider />
            <Row v-if="isImgQuestion">
               <Col class="img-box" span="24">
                    <img class="question-img" src='../assets/action.jpg'/>
               </Col>
            </Row>
            <Divider v-if="isImgQuestion" />
            <Row>
              <Col span="12" class="select-item" :class="optionAclass" @click.native="chooseA"><span v-if="optionA.active">A. </span>{{optionA.content}}</Col>
              <Col span="12" class="select-item" :class="optionBclass" @click.native="chooseB"><span v-if="optionB.active">B. </span>{{optionB.content}}</Col>
            </Row>
            <Row>
              <Col span="12" class="select-item" :class="optionCclass" @click.native="chooseC"><span v-if="optionC.active">C. </span>{{optionC.content}}</Col>
              <Col span="12" class="select-item" :class="optionDclass" @click.native="chooseD"><span v-if="optionD.active">D. </span>{{optionD.content}}</Col>
            </Row>
          </Row>
          <div class="btn-box">
            <ButtonGroup shape="circle">
                <Button :loading="previousLoading" type="primary" @click="previousOne">
                    <Icon type="ios-arrow-back"></Icon>
                    上一题
                </Button>
                <!-- <Button type="success">
                    确认
                </Button> -->
                <Button :loading="nextLoading" type="primary" @click="nextOne">
                    下一题
                    <Icon type="ios-arrow-forward"></Icon>
                </Button>
            </ButtonGroup>
        </div>
        </div>
        
    </Card>
 </div>
</template>

<script>
import QCbus from "./QCbus.js";
export default {
    data() {
        return {
            isImgQuestion: false,
            spinShow: false,
            previousLoading: false,
            nextLoading: false,
            optionAclass: "",
            optionBclass: "",
            optionCclass: "",
            optionDclass: "",
            questionTitle: "",
            optionA: {
                index: 1,
                content: "默认选项A"
            },
            optionB: {
                index: 2,
                content: "默认选项B"
            },
            optionC: {
                index: 3,
                content: "默认选项C"
            },
            optionD: {
                index: 4,
                content: "默认选项D"
            },
            recentQuesObj: null
        };
    },
    beforeMount() {
        console.log(
            `构建习题组建，习题的内部序号${this.recentIndex},习题的数据库id${
                this.recent
            }`
        );
        let qid = this.recent;
        this.requestQuestion(qid);
    },
    watch: {
        recent: function(val, oldVal) {
            console.log("recent 变化了");
            if (val !== oldVal) {
                this.requestQuestion(val);
            }
        }
    },
    props: {
        type: String,
        scene: String,
        recent: String,
        recentIndex: Number
    },
    components: {},
    methods: {
        // 下一题
        nextOne() {
            let total = this.$exam.totalQues;
            console.log(total);
            console.log(this.recentIndex);
            if (this.recentIndex < total) {
                this.$emit("next-one");
                this.nextLoading = true;
            } else {
                this.$Message.info("已经是最后一题");
            }
        },
        // 上一题
        previousOne() {
            if (this.recentIndex > 1) {
                this.$emit("previous-one");
                this.previousLoading = true;
            } else {
                this.$Message.info("已经是第一题了");
            }
        },
        dealWithQuestion(questionObj) {
            console.log(
                `recent question ${this.recent}--------------->`,
                questionObj
            );
            this.questionTitle = questionObj.questionTitle;
            this.optionA = {
                index: 1,
                active: questionObj.optionA ? true : false,
                content: questionObj.optionA
            };
            this.optionB = {
                index: 2,
                active: questionObj.optionB ? true : false,
                content: questionObj.optionB
            };
            this.optionC = {
                index: 3,
                active: questionObj.optionC ? true : false,
                content: questionObj.optionC
            };
            this.optionD = {
                index: 4,
                active: questionObj.optionD ? true : false,
                content: questionObj.optionD
            };
            console.log(this.optionD);
            if (questionObj.questionForm > 0) {
                this.isImgQuestion = true;
            } else {
                this.isImgQuestion = false;
            }
            this.displayAnswerHistory(questionObj.questionId);
        },
        requestQuestion(qid) {
            this.spinShow = true;
            if (this.$exam.checkIfCached(qid)) {
                console.log("--------------->命中缓存");

                this.nextLoading = false;
                this.previousLoading = false;
                this.spinShow = false;
                this.resetClass();
                let questionObj = this.$exam.getQuestion(qid);
                this.dealWithQuestion(questionObj);

                this.cacheNextQuestion();
            } else {
                switch (this.scene) {
                    case "practice":
                        this.$http
                            .get(
                                `/api/exercise/getQuestionById?questionId=${qid}`
                            )
                            .then(res => {
                                console.log("getQesById", res.data);
                                if (res.data.status == 200) {
                                    this.nextLoading = false;
                                    this.previousLoading = false;
                                    this.spinShow = false;
                                    this.resetClass();
                                    let questionObj = res.data.data;
                                    this.$exam.cacheQuestion(questionObj);
                                    this.dealWithQuestion(questionObj);
                                    this.cacheNextQuestion();
                                }
                            });
                        break;
                    case "mock":
                        this.$http
                            .get(
                                `/api/practiceTest/getQuestionById?questionId=${qid}`
                            )
                            .then(res => {
                                console.log("getQesById", res.data);
                                if (res.data.status == 200) {
                                    this.nextLoading = false;
                                    this.previousLoading = false;
                                    this.spinShow = false;
                                    this.resetClass();
                                    let questionObj = res.data.data;
                                    this.$exam.cacheQuestion(questionObj);
                                    this.dealWithQuestion(questionObj);
                                    this.cacheNextQuestion();
                                }
                            });
                        break;
                    case "exam":
                        this.$http
                            .get(
                                `/api/practiceTest/getQuestionById?questionId=${qid}`
                            )
                            .then(res => {
                                console.log("getQesById", res.data);
                                if (res.data.status == 200) {
                                    this.nextLoading = false;
                                    this.previousLoading = false;
                                    this.spinShow = false;
                                    this.resetClass();
                                    let questionObj = res.data.data;
                                    this.$exam.cacheQuestion(questionObj);
                                    this.dealWithQuestion(questionObj);
                                    this.cacheNextQuestion();
                                }
                            });
                        break;

                    default:
                        break;
                }
            }
        },
        cacheNextQuestion() {
            let nextId = this.$exam.getQuestionIdByIndex(this.recentIndex + 1);
            if (!nextId || this.$exam.checkIfCached(nextId)) return;
            console.log(`next Id is ${nextId}`);
            switch (this.scene) {
                case "practice":
                    this.$http
                        .get(
                            `/api/exercise/getQuestionById?questionId=${nextId}`
                        )
                        .then(res => {
                            console.log("预加载成功", res.data);
                            if (res.data.status == 200) {
                                let questionObj = res.data.data;
                                this.$exam.cacheQuestion(questionObj);
                            }
                        });
                    break;
                case "mock":
                    this.$http
                        .get(
                            `/api/practiceTest/getQuestionById?questionId=${nextId}`
                        )
                        .then(res => {
                            console.log("getQesById", res.data);
                            if (res.data.status == 200) {
                                let questionObj = res.data.data;
                                this.$exam.cacheQuestion(questionObj);
                            }
                        });
                    break;
                case "exam":
                    this.$http
                        .get(
                            `/api/practiceTest/getQuestionById?questionId=${nextId}`
                        )
                        .then(res => {
                            console.log("getQesById", res.data);
                            if (res.data.status == 200) {
                                let questionObj = res.data.data;
                                this.$exam.cacheQuestion(questionObj);
                            }
                        });
                    break;
                default:
                    break;
            }
        },
        // 用来处理已经答过的题目的展示
        displayAnswerHistory(qid) {
            if (this.$exam.checkAnswer(qid)) {
                let answer = this.$exam.answerMap[qid];
                switch (this.scene) {
                    case "practice":
                        if (answer.doneType === 1) {
                            this.displayYesOrNo(true, answer.rightIndex);
                        } else if (answer.doneType === 0) {
                            this.displayYesOrNo(
                                false,
                                answer.rightIndex,
                                answer.wrongIndex
                            );
                        }
                        break;
                    case "mock":
                        if (answer.done) {
                            this.signDone(answer.selection);
                        }
                        break;
                    case "exam":
                        if (answer.done) {
                            this.signDone(answer.selection);
                        }
                        break;

                    default:
                        break;
                }
            }
        },
        collection() {
            console.log("收藏");
        },
        chooseA() {
            if (!this.optionA.active) return;
            this.dealChoose(this.optionA.index);
        },
        chooseB() {
            if (!this.optionB.active) return;
            this.dealChoose(this.optionB.index);
        },
        chooseC() {
            if (!this.optionC.active) return;
            this.dealChoose(this.optionC.index);
        },
        chooseD() {
            if (!this.optionD.active) return;
            this.dealChoose(this.optionD.index);
        },
        saveChoose() {},
        // 根据不同场合处理选择，训练模式，直接发送答案并判断对错
        // 如果是正式考试或者模拟考试,则在存储答案
        dealChoose(index) {
            let rightAnswer;
            this.resetClass();
            switch (this.scene) {
                case "practice":
                    rightAnswer = this.$exam.getQuestion(this.recent).answer;
                    if (rightAnswer === index) {
                        console.log("答对了");
                        this.displayYesOrNo(true, index);
                        this.$exam.markRight(this.recent, index);
                        QCbus.$emit("answer-correct", {
                            qid: this.recent
                        });
                    } else {
                        console.log("答错了");
                        this.displayYesOrNo(false, rightAnswer, index);
                        this.$exam.markWrong(this.recent, rightAnswer, index);
                        QCbus.$emit("answer-wrong", {
                            qid: this.recent
                        });
                    }
                    this.$http
                        .get(
                            `/api/exercise/saveAnswer?questionId=${
                                this.recent
                            }&userOption=${index}`
                        )
                        .then(res => {
                            console.log("保存成功", res.data);
                        });

                    break;
                case "mock":
                    this.$exam.markDone(this.recent, index);
                    this.$exam.fillDoneList(this.recent, this.recentIndex);

                    this.signDone(index);

                    QCbus.$emit("answer-done", {
                        qid: this.recent
                    });
                    break;

                case "exam":
                    this.$http
                        .post("/api/formalExam/saveOneFormalExam", {
                            questionId: this.recent,
                            userOption: index
                        })
                        .then(res => {
                            if (res.data.status == 200) {
                                console.log("同步答案成功", this.recent, index);
                            } else {
                                this.$Message.error("同步答案出错");
                            }
                        })
                        .catch(err => {
                            console.log(err);
                            this.$Message.error("同步答案出错");
                        });
                    this.$exam.markDone(this.recent, index);
                    this.$exam.fillDoneList(this.recent, this.recentIndex);

                    this.signDone(index);
                    QCbus.$emit("answer-done", {
                        qid: this.recent
                    });
                    break;
                default:
                    break;
            }
        },
        resetClass() {
            this.optionAclass = "";
            this.optionBclass = "";
            this.optionCclass = "";
            this.optionDclass = "";
        },
        displayYesOrNo(ifYes, yesIndex, noIndex) {
            if (ifYes) {
                if (this.optionA.index === yesIndex) {
                    this.optionAclass = "correct";
                } else if (this.optionB.index === yesIndex) {
                    this.optionBclass = "correct";
                } else if (this.optionC.index === yesIndex) {
                    this.optionCclass = "correct";
                } else if (this.optionD.index === yesIndex) {
                    this.optionDclass = "correct";
                }
            } else {
                if (this.optionA.index === yesIndex) {
                    this.optionAclass = "correct";
                } else if (this.optionB.index === yesIndex) {
                    this.optionBclass = "correct";
                } else if (this.optionC.index === yesIndex) {
                    this.optionCclass = "correct";
                } else if (this.optionD.index === yesIndex) {
                    this.optionDclass = "correct";
                }
                if (this.optionA.index === noIndex) {
                    this.optionAclass = "wrong";
                } else if (this.optionB.index === noIndex) {
                    this.optionBclass = "wrong";
                } else if (this.optionC.index === noIndex) {
                    this.optionCclass = "wrong";
                } else if (this.optionD.index === noIndex) {
                    this.optionDclass = "wrong";
                }
            }
        },
        signDone(selection) {
            // 考试模式下，标记做了与否
            if (this.optionA.index === selection) {
                this.optionAclass = "selected";
            } else if (this.optionB.index === selection) {
                this.optionBclass = "selected";
            } else if (this.optionC.index === selection) {
                this.optionCclass = "selected";
            } else if (this.optionD.index === selection) {
                this.optionDclass = "selected";
            }
        },
        resetQuestion() {
            this.resetClass();
            this.$exam.resetPerAnswer(this.recent);
            QCbus.$emit("answer-clear", {
                qid: this.recent
            });
        }
    },

    beforeDestroy() {},
    destroyed() {}
};
</script>

