<style scoped>
.left-index {
    padding: 20px;
}
.right-question {
    padding: 20px;
}
</style>
<template>
  <Row>
    <Col class="left-index" span="6">
      <Qindex :scene="scene" @block-click="blockClick" :quesArr="quesArr"/>
    </Col>
    <Col class="right-question" span="18">
        <Question :recent="recent" @next-one="clickNextOne" @previous-one="clickPreviousOne" :recentIndex="recentIndex" :scene="scene" type="single" />
    </Col>
  </Row>
</template>

<script>
import Question from "../components/Question";
import Qindex from "../components/QuestionIndex";
import QCBus from "../components/QCbus.js";
export default {
    data() {
        return {
            recent: "",
            recentIndex: 1
        };
    },

    beforeMount() {
        console.log("control quesArr", this.quesArr);
        this.recent = this.quesArr[0].toString();
        // this.recentIndex 是题号，从1开始
        this.recentIndex = 1;
    },
    beforeDestroy() {
        console.log("qs control destroyed");
    },
    props: {
        quesArr: Array,
        scene: String
    },
    components: {
        Question,
        Qindex
    },
    methods: {
        blockClick(arg) {
            console.log("in q control child block be clicked", arg);
            let clickedIndex = arg.index;
            this.recent = this.quesArr[clickedIndex - 1].toString();
            this.recentIndex = clickedIndex;
        },
        clickNextOne() {
            let total = this.$exam.totalQues;
            if (
                this.recentIndex > (this.quesArr.length-6) &&
                this.recentIndex <= this.quesArr.length &&
                this.quesArr.length < total
            ) {
                QCBus.$emit("load-more");
            } 
            if (this.recentIndex >= this.quesArr.length) return;
            else {
                this.recentIndex++;
                this.recent = this.quesArr[this.recentIndex - 1].toString();
            }
        },
        clickPreviousOne() {
            if (this.recentIndex <= 1) return;
            this.recentIndex--;
            this.recent = this.quesArr[this.recentIndex - 1].toString();
        }
    }
};
</script>

