<style scoped>
.layout {
    background-color: rgba(255, 255, 255, 0.3);
}
</style>
<template>
 <MainLayout :isIndex="true" class="layout">
   <router-view></router-view>
 </MainLayout>
</template>

<script>
import MainLayout from "../Layout/Layout";
export default {
    data() {
        return {};
    },
    beforeMount() {
        this.getDeptTree().then(res => {
            if (res.data.status == 200) {
                console.log("SaveDeptTree", res.data.data);
                this.$store.dispatch("SaveDeptTree", res.data.data);
            }
        });
        this.getDept().then(res => {
            if (res.data.status == 200) {
                console.log("SaveDeptMap", res.data.data);
                this.$store.dispatch("SaveDeptMap", res.data.data);
            }
        });
    },
    components: {
        MainLayout
    },
    methods: {
        getDeptTree() {
            return this.$http.get("/api/manage/getDeptNodeTree");
        },
        getDept() {
            return this.$http.get("/api/manage/getDeptNodes");
        }
    }
};
</script>

