<style scoped>
.default {
  display: inline-block;
  width: 36px;
  height: 28px;
  border: 1px solid #ccc;
  line-height: 28px;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
  margin: 2px;
  user-select: none;
}

.undo-class {
  background-color: #eee;
}
.done-class {
  background-color: skyblue;
}

.correct-class {
  background-color: greenyellow;
}

.wrong-class {
  background-color: red;
}
</style>

<template>
  <div @click="beClicked" class="default undo-class" :class="colorClass">
    {{index}}
  </div>
</template>

<script>
import QCbus from './QCbus.js'
export default {
  data() {
    return {

    };
  },
  props: {
    index: Number,
    qid:Number,
    status:String
  },
  components: {},
  beforeMount(){

  },
  methods: {
    beClicked() {
      console.log(`block ${this.index} be click`);
      this.$emit("block-click", {
        index: this.index
      });
    }
  },
  computed: {
    colorClass: function() {
      switch (this.status) {
        case "default":
          return "undo-class";
        case "done":
          return "done-class";
        case "correct":
          return "correct-class";
        case "wrong":
          return "wrong-class";
        default:
          break;
      }
    }
  },
  beforeDestroy() {
    
  },
};
</script>

