<style scoped>
.btn-hand-in {
    width: 60px;
}
</style>
<template>
  <Card :bordered="false" :padding="12">
      <span slot="title">考生信息</span>
      <Button v-if="scene!=='practice'" class="btn-hand-in" size="small" slot="extra" type="success" @click="handInPaper">交卷</Button>
        <Collapse>
        <Panel name="1">
            {{realName}} ({{stuId}})
            <Row slot="content">
              <Col span="4">
                <b>院系：</b>
              </Col>
              <Col span="4">
                {{academy}}
              </Col>
              <Col span="4">
                <b>年级：</b>
              </Col>
              <Col span="4">
                {{grade}}
              </Col>
              <Col span="4">
                <b>班级：</b>
              </Col>
              <Col span="4">
                {{classs}}
              </Col>
            </Row>
        </Panel>
      </Collapse>
  </Card>
</template>

<script>
// 这部分的信息应该从vux里去获取登陆的用户的信息，来显示
import { mapState } from "vuex";
export default {
    data() {
        return {
            realName: "",
            stuId: "",
            academy: "",
            grade: "",
            classs: ""
        };
    },
    computed: {
        ...mapState(["userInfo"])
    },
    components: {},
    props: {
        scene: String
    },
    beforeMount() {
        let deptArr = this.$store.getters.getDeptMap;
        let deptInfo = this.$store.getters.getDeptInfo;
        let infoArr = deptInfo.split(",");
        this.infoArr = infoArr;
        console.log(infoArr);
        this.turnToMap(deptArr);
        this.getStuInfo();
    },
    methods: {
        turnToMap(deptArr) {
            this.deptMap = {};
            deptArr.forEach(item => {
                this.deptMap[item.deptId] = item.deptName;
            });
        },
        getStuInfo() {
            this.realName = this.$store.getters.realName;
            this.stuId = this.$store.getters.stuId;
            this.classs = this.deptMap[this.infoArr[2]];
            this.grade = this.deptMap[this.infoArr[1]];
            this.academy = this.deptMap[this.infoArr[0]];
            if (!this.realName || !this.stuId) {
                this.$http.get("/api/manage/getCurrentUser").then(res => {
                    if (res.data.status == 200) {
                        let userInfo = res.data.data;
                        this.realName = userInfo.realName;
                        this.stuId = userInfo.userNum;
                        this.classs = this.deptMap[userInfo.classId];
                        this.grade = this.deptMap[userInfo.gradeId];
                        this.academy = this.deptMap[userInfo.academyId];
                        this.userInfo = userInfo;
                    }
                });
            }
        },
        handInPaper() {
            console.log("交卷");
            let answerObj = this.$exam.getAnswers();
            let answerArr = this.dealTheAnswers(answerObj);
            let already = answerArr.length;
            let total = this.$exam.questionNum;
            this.$Modal.confirm({
                title: "交卷提醒",
                content: `<p>亲爱的考生,本次考试一共${total}道题,你已做完${already}道,确认交卷?</p>`,
                onOk: () => {
                    console.log("点击确认，提交答案");
                    this.sendAnswerArr(answerArr);
                },
                onCancel: () => {}
            });
        },
        dealTheAnswers(answerObj) {
            let { doneList, answers } = answerObj;
            let answerArr = [];
            doneList.forEach(item => {
                if (item) {
                    let temp = {};
                    // temp.index = index;
                    temp.questionId = item;
                    temp.userOption = answers[item].selection;
                    answerArr.push(temp);
                }
            });
            return answerArr;
        },
        sendAnswerArr(answerArr) {
            if (this.scene === "mock") {
                // /api/practiceTest/submitPracticeTest
                this.$http
                    .post("/api/practiceTest/submitPracticeTest", {
                        userAnswer: answerArr
                    })
                    .then(res => {
                        console.log("交卷以后", res.data);
                        if (res.data.status == 200) {
                            let data = res.data.data;
                            let result = data.resultState ? "通过" : "未通过";
                            let word = data.resultState ? "恭喜" : "请再接再厉";
                            let title = "考试结果";
                            let content = `<p>您本次考试的分数为：<span style="color:red;">${
                                data.total
                            }</span> 分，<b>${result}</b>本次考试，${word}！</p>`;
                            setTimeout(() => {
                                this.$Modal.success({
                                    title: title,
                                    content: content,
                                    okText: "返回首页",
                                    onOk: () => {
                                        console.log("交卷完毕，返回主页");
                                        this.$router.push("/index/");
                                    }
                                });
                            }, 500);
                        }
                    });
            } else if (this.scene === "exam") {
                this.$http.get("/api/formalExam/submitFormalExam").then(res => {
                    console.log("正式考试交卷后", res.data);
                    if (res.data.status == 200) {
                        this.$emit("hand-success");
                        setTimeout(() => {
                            this.$Modal.success({
                                title: "交卷成功",
                                content: "点击个人中心查看成绩",
                                okText: "个人中心",
                                onOk: () => {
                                    this.$router.push("/index/selfcenter");
                                }
                            });
                        }, 200);
                    }
                });
            } else {
                console.log("没有这种情况");
            }
        }
    }
};
</script>

