<style scoped>
.notice {
    margin: 20px;
    margin-bottom: 0px;
}
</style>
<template>
 <div>
   <Alert class="notice" type="warning" show-icon closable>模拟考试过程中请勿离开或刷新(F5)页面，否则考试立即结束</Alert>
   <Row>
     <Row>
        <Col style="padding:20px 20px 0 20px;" span="6">
          <Timer :time="time" />
        </Col> 
        <Col style="padding:20px 20px 0 20px;" span="18">
          <StuInfo scene="mock" />
        </Col> 
     </Row>
     <QstCtl scene="mock" :quesArr="questionArr" v-if="questionArr.length"/>
   </Row>
 </div>
</template>

<script>
import QstCtl from "../components/QuestionControl";
import StuInfo from "../components/StudentInfo";
import Timer from "../components/ExamTimer";
export default {
    data() {
        return {
            questionArr: [],
            time: 0
        };
    },
    async beforeMount() {
        this.$exam.initControl("mock", 1);
        this.$http.get("/api/practiceTest/startPracticeTest").then(res => {
            let totalTime = parseInt(res.data.data.ruleTime) * 60;
            this.time = totalTime;
            console.log(this.time);
            let arr = res.data.data.resultList;
            this.$exam.totalQues = arr.length;
            this.questionArr = arr;
            this.$exam.setQuestionArr(arr);
        });
    },
    components: {
        QstCtl,
        StuInfo,
        Timer
    },
    methods: {
        getRecentAnswer() {
            console.log("getRecentAnswers");
            let answers = this.$exam.getAnswers();
            console.log("all answers", answers);
        },
        // 强制交卷或主动交卷
        handInPaper() {}
    },
    beforeDestroy() {},
    destroyed() {}
};
</script>