<style scoped>
.notice {
  margin: 20px;
  margin-bottom: 0px;
}
.done-class {
  background-color: skyblue;
}

.correct-class {
  background-color: greenyellow;
}

.wrong-class {
  background-color: red;
}
</style>
<template>
 <div>
   <Alert class="notice" type="warning" show-icon closable>已做过显示为<b class="done-class">蓝色</b>,正确显示为<b class="correct-class">绿色</b>,错误显示为<b class="wrong-class">红色</b></Alert>
   
   <Row>
     <Row>
       <Col style="padding:20px 20px 0 20px;" span="6">
          <PracTool />
        </Col> 
        <Col style="padding:20px 20px 0 20px;" span="18">
          <StuInfo scene="practice" />
        </Col> 
     </Row>
     <QstCtl scene="practice" :quesArr="questionArr" v-if="questionArr.length"/>

   </Row>
 </div>
</template>

<script>
if (!window.setImmediate) {
  window.setImmediate = function(func, args) {
    return window.setTimeout(func, 0, args);
  };
  window.clearImmediate = window.clearTimeout;
}
import QstCtl from "../components/QuestionControl";
import StuInfo from "../components/StudentInfo";
import PracTool from "../components/PracticeTool";
import QCbus from "../components/QCbus.js";
export default {
  data() {
    return {
      questionArr: [],
      page:1,
      maxPage:0,
      pageSize:100
    };
  },
  async beforeMount() {
    QCbus.$on("load-more", () => {
      console.log("received load more");
      this.getNextPage()
    });
    this.$exam.initControl("practice", 2);
    //let res=await this.$http.get('/getTestQuestions');
    this.getQuesForOneSelf().then(res => {
      console.log("--------------->", res.data);
      if (res.data.status == 200) {
        let data = res.data.data;
        let total = data.count;
        // 在控制器中设置所有题目的总和
        this.$exam.totalQues = total;
        this.maxPage = Math.ceil(total/this.pageSize);
        let quesArr = data.questionList;
        this.formatQuesArr(quesArr)
      } else {
        this.$Message.error("网络错误");
      }
    });
    //  console.log('getTestQuestions',res.data)
    //  let arr = res.data.questionArr;
    //  this.questionArr = arr;
  },
  components: {
    QstCtl,
    StuInfo,
    PracTool
  },
  methods: {
    formatQuesArr(quesArr) {
      let newQuesArr = [];
      // 强制按question_id排序
      quesArr.sort((a, b) => {
        return a.question_id - b.question_id;
      });
      quesArr.forEach(item => {
        newQuesArr.push(item.question_id);
        this.$exam.setUserOptionForPrac(item.question_id, item.user_option);
        if (item.user_option > 0) {
          window.setImmediate(() => {
            QCbus.$emit("answer-done-id", {
              qid: item.question_id
            });
          });
        }
      });

      this.questionArr = this.questionArr.concat(newQuesArr);
      this.$exam.appendMoreIntoArr(newQuesArr);
    },
    getNextPage(){
      this.page++;
      if (this.page>this.maxPage) {
        this.$Message.info('没有更多题目')
      } else {
        this.getQuesForOneSelf().then(res=>{
        if (res.data.status == 200) {
          console.log('获取更多试题')
          let data = res.data.data;
        // 在控制器中设置所有题目的总和
          let quesArr = data.questionList;
          this.formatQuesArr(quesArr)
        } else {
            this.$Message.error("网络错误");
        }
      })
      }
      
    },
    getQuesForOneSelf() {
      return this.$http.get(
        `/api/exercise/getQuestionsByPage?page=${this.page}&row=${this.pageSize}`
      );
    }
  },
  beforeDestroy() {
    QCbus.$off("load-more");
  },
  destroyed() {
    console.log("++++++++++++++Practice destroyed+++++++++++++");
  }
};
</script>

