<style scoped>
.data-set {
text-align: center;
margin-top:10px;
font-size: 14px;
}
</style>
<template>
 <div>
    <Card>
        <Row>
            <Col style="text-align: center;" span="24">
            {{dateString}}
            </Col>
        </Row>
        <Row class="data-set">
            <Col span="12">
              总题数
            </Col>
            <Col span="12">
              {{total}}
            </Col>
        </Row>
    </Card>
 </div>
</template>

<script>
export default {
  data() {
    return {
      dateString: "",
      total:0
    };
  },
  beforeMount() {
    this.setDateString()
    let interFlag = setInterval(()=>{
      this.total = this.$exam.totalQues;
      if (this.total!==0) {
        clearInterval(interFlag)
      }
    },2000)
  },
  components: {},
  methods: {
    setDateString() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let week = date.getDay();
      let weekString;
      switch (week) {
        case 0:
          weekString = "天";
          break;
        case 1:
          weekString = "一";
          break;
        case 2:
          weekString = "二";
          break;
        case 3:
          weekString = "三";
          break;
        case 4:
          weekString = "四";
          break;
        case 5:
          weekString = "五";
          break;
        case 6:
          weekString = "六";
          break;
        default:
          break;
      }
      this.dateString = `${year}年${month}月${day}日 星期${weekString}`
    }
  }
};
</script>

