<style scoped>
.counter {
    color: red;
    font-size: 40px;
    text-align: center;
}
</style>
<template>
 <div>
    <Card>
        <Row>
            <Col style="text-align: center;" span="24">
            {{dateString}}
            </Col>
        </Row>
        <Row>
            <Col class="counter" span="24">
                {{minute}}:{{second}}
            </Col>
        </Row>
        
    </Card>
 </div>
</template>

<script>
export default {
  data() {
    return {
      dateString: "2018年9月20日 星期四",
      remainSeconds: 3000 ,//剩余时间  3000
      intervalFlag:null,
      minute:0,
      second:'0',
    };
  },
  beforeMount() {
    this.setDateString()
    //this.beginToCount(5)
    
  },
  props:{
    time:Number
  },
  watch:{
    time:function(n,o){
      console.log('time change',o,n)
      this.beginToCount(this.time)
    }
  },
  components: {

  },
  beforeDestroy(){
    this.stopCount()
  },
  methods: {
    stopCount(){
      clearInterval(this.intervalFlag)
    },
    beginToCount(seconds) {
        this.stopCount()
        this.remainSeconds =seconds;
         this.convertSecondsDisplay()
        this.intervalFlag = setInterval(()=>{
            this.remainSeconds--
            if (this.remainSeconds<=0) {
              this.stopCount()
              this.$Modal.confirm({
                title:"考试时间到",
                content:'如果是模拟考试，请主动交卷，如果是正式考试，后台会自动交卷，请稍后前往个人中心查看成绩。'
              })
            }
            this.convertSecondsDisplay()
        },1000)
    },
    convertSecondsDisplay(){
        let second = this.remainSeconds%60;
        if(second<10) {
            this.second = `0${second}`;
        } else {
            this.second = second.toString()
        }
        this.minute = Math.floor(this.remainSeconds/60)
    },
    setDateString() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let week = date.getDay();
      let weekString;
      switch (week) {
        case 0:
          weekString = "天";
          break;
        case 1:
          weekString = "一";
          break;
        case 2:
          weekString = "二";
          break;
        case 3:
          weekString = "三";
          break;
        case 4:
          weekString = "四";
          break;
        case 5:
          weekString = "五";
          break;
        case 6:
          weekString = "六";
          break;
        default:
          break;
      }
      this.dateString = `${year}年${month}月${day}日 星期${weekString}`
    }
  }
};
</script>

