<style scoped>
.edit-btn {
}
.item-title {
    font-weight: bold;
}
.person-info {
    margin-bottom: 10px;
}

.info-content {
    text-align: center;
}

.school-info {
}

.link-like {
    color: #2d8cf0;
    text-decoration: underline;
    cursor: pointer;
}

.record-item {
    margin-top: 8px;
    margin-bottom: 8px;
}

.color-red {
    color: #ed4014;
}

.color-green {
    color: #19be6b;
}

.info-card {
    margin-top: 15px;
    margin-bottom: 15px;
}
.quetion-id {
    color: #2d8cf0;
    text-decoration: underline;
    cursor: pointer;
}
.err-item-row {
    margin-bottom: 5px;
}
</style>
<template>
 <div>
   
   <Row class="info-card">
     <Col push="5" span="14">
      <Card>
          <p slot="title">个人信息</p>
          <Button size="small" slot="extra" type="primary" @click="editInfo">修改信息</Button>
          <Button size="small" slot="extra" style="margin-left:10px;" type="error" @click="editPwd">修改密码</Button>
          <div class="info-content">
              <Row class="person-info">
                <Col class="item-title" span="3">
                  姓名：
                </Col>
                <Col span="5">
                  {{realName}}
                </Col>
                <Col class="item-title" span="3">
                  学号：
                </Col>
                <Col span="5">
                  {{stuId}}
                </Col>
                <Col class="item-title" span="3">
                  性别：
                </Col>
                <Col v-if="sexShow" span="5">
                  {{sex>0?'女':'男'}}
                </Col>
              </Row>
              <Row class="school-info">
                <Col class="item-title" span="3">
                  院系：
                </Col>
                <Col span="5">
                  {{academy}}
                </Col>
                <Col class="item-title" span="3">
                  年级：
                </Col>
                <Col span="5">
                  {{grade}}
                </Col>
                <Col class="item-title" span="3">
                  班级：
                </Col>
                <Col span="5">
                  {{classs}}
                </Col>
              </Row>
              
          </div>
      </Card>
    </Col>
   </Row>
    <Row>
     <Col push="5" span="14">
      <Collapse v-model="CollapseIndex">
        <Panel name="1">
            考试记录
            <div style="text-align:center" slot="content">
              <Row style="font-weight:bold">
                <Col span="6">
                    考试时间
                </Col>
                <Col span="4">
                    考试状态
                </Col>
                <Col span="4">
                    考试结果
                </Col>
                <Col span="4">
                    考试成绩
                </Col>
                <Col span="6">
                    查看错题
                </Col>
              </Row>
              <Row v-for="item in recordsArr" :key="item.index" class="record-item">
                <Col span="6">
                    {{item.time}}
                </Col>
                <Col span="4">
                    {{item.status}}
                </Col>
                <Col :class="item.result>0?'color-green':'color-red'" span="4">
                    {{item.result>0?'通过':'未通过'}}
                </Col>
                <Col span="4">
                    {{item.score}}
                </Col>
                <Col @click.native="seeDetail(item.reportId)" class="link-like" span="6">
                    点击查看
                </Col>
              </Row>
            </div>
        </Panel>
    </Collapse>
    </Col>
   </Row>
    <Modal v-model="modifyModal" width="500">
        <p slot="header">
            <span>修改信息</span>
        </p>
        <div>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title">姓名：</p>
                  <Input v-model="modifyInfo.realName" placeholder="姓名" style="width: 300px" />
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title">学号：</p>
                  <Input disabled v-model="modifyInfo.userNum" placeholder="学号" style="width: 300px" />
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                <p class="input-item-title">性别：</p>
                <RadioGroup v-model="modifyInfo.sex">
                    <Radio :label="0">男</Radio>
                    <Radio :label="1">女</Radio>
                </RadioGroup>
                </Col>
            </Row>
            <Row>
                <Col class="input-item" span="24">
                  <p class="input-item-title">院系/年级/班级：</p>
                  <Cascader style="width: 300px" :data="cascaderData" v-model="modifyInfo.deptArr"></Cascader>
                </Col>
            </Row>
        </div>
        <div slot="footer">
            <Button type="success" @click="confirmModify">确认</Button>
        </div>
    </Modal>
    <Modal style="text-align:center;" v-model="erroModal" width="600">
      <p slot="header">
        <span>错题详情(点击题号查看题目详情)</span>
      </p>
      <div>
        <Row style="font-weight:bold;">
             <Col span="6">
               题号
             </Col>
             <Col span="6">
               分值
             </Col>
             <Col span="6">
               你的选择
             </Col>
             <Col span="6">
               正确答案
             </Col>
        </Row>
        <Row class="err-item-row" v-for="item in displayError" :key="item.qid">
           <Col @click.native="seeQuestion(item.qid)" class="quetion-id" span="6">
             {{item.qid}}
           </Col>
           <Col span="6">
             {{item.score}}
           </Col>
           <Col span="6">
             {{item.you}}
           </Col>
           <Col span="6">
             {{item.right}}
           </Col>
        </Row> 
      </div>
    </Modal>
    <Modal v-model="questionModal">
      <p slot="header">
        <span>题目详情(题号{{questionDetail.questionId}})</span>
        <div>
            <Row>
               <Col span="24">
                  [{{questionDetail.questionTypeName}}]：{{questionDetail.questionTitle}}
               </Col>
            </Row>
            <Row>
                <Col span="12">
                  A:{{questionDetail.optionA}}
                </Col>
                <Col span="12">
                   B:{{questionDetail.optionB}}
                </Col>
                <Col span="12">
                   C:{{questionDetail.optionC}}
                </Col>
                <Col span="12">
                   D:{{questionDetail.optionD}}
                </Col>
            </Row>
        </div>
      </p>
 </Modal>
 </div>
 
</template>

<script>
import { secondsToDateString } from "../utils/dateTools.js";
export default {
    data() {
        return {
            sexShow:false,
            questionModal: false,
            questionDetail: {
                questionId: 0,
                questionTitle: "",
                optionA: "",
                optionB: "",
                optionC: "",
                optionD: "",
                questionForm:0,
                questionTypeName:'数学题'
            },
            erroModal: false,
            modifyInfo: {
                realName: "",
                userNum: "",
                sex: 0,
                deptArr: []
            },
            rowData: [],
            displayError: [],
            modifyModal: false,
            cascaderData: [],
            resultMap: {},
            realName: "",
            stuId: "",
            academy: "",
            grade: "",
            classs: "",
            sex: null,
            userInfo: {
                userType: 5,
                userNum: "",
                userName: "",
                realName: "",
                deptArr: [],
                academyId: 0,
                gradeId: 0,
                classId: 0,
                sex: 0
            },
            CollapseIndex: "1",
            recordsArr: [

            ]
        };
    },
    beforeMount() {
        this.getDept().then(res => {
            console.log("getDeptNodes", res.data);
            if (res.data.status == 200) {
                this.turnToMap(res.data.data);
                this.getStuInfo();
            }
        });
        this.getDeptTree().then(res => {
            if (res.data.status == 200) {
                let treeArr = deleteUn(res.data.data);
                this.cascaderData = traceDeptTree(treeArr);
            }
        });
        this.getExamResult();
    },
    components: {},
    methods: {
        seeQuestion(qid) {
          console.log("查看题目", qid);
            this.$http
                .get(`/api/exercise/getQuestionById?questionId=${qid}`)
                .then(res => {
                    console.log("getQesById", res.data);
                    if (res.data.status == 200) {
                      let ques = res.data.data
                        console.log(res.data)
                        this.questionDetail = ques;

                        this.questionModal =true
                    } else {
                      this.$Message.error('该题不存在，可能已被删除')
                    }
                });
        },
        getExamResult() {
            this.$http.get("/api/manage/queryMyAllReports").then(res => {
                console.log("queryMyAllReports", res.data);
                if (res.data.status == 200) {
                    console.log("个人成绩", res.data.data);
                    let resultArr = res.data.data;
                    this.formatExamResult(resultArr);
                    this.rowData = resultArr;
                }
            });
        },
        formatExamResult(resultArr) {
            this.recordsArr = [];
            resultArr.forEach(item => {
                let temp = {};
                temp.score = item.userScore;
                temp.result = item.resultState;
                temp.time = secondsToDateString(item.startTime);
                temp.status = item.examState > 0 ? "已结束" : "考试中";
                temp.reportId = item.reportId;
                this.resultMap[item.reportId] = item;
                this.recordsArr.push(temp);
            });
        },

        seeDetail(reportId) {
            
            let errorJsonArr = JSON.parse(
                this.resultMap[reportId].errorAnswersJson
            );
            this.formatErrorData(errorJsonArr);
            this.erroModal = true;
        },
        formatErrorData(errorJsonArr) {
            this.displayError = [];
            errorJsonArr.forEach(item => {
                let temp = {};
                temp.qid = item.questionId;
                switch (item.userOption) {
                    case 0:
                        temp.you = "未做";
                        break;
                    case 1:
                        temp.you = "A";
                        break;
                    case 2:
                        temp.you = "B";
                        break;
                    case 3:
                        temp.you = "C";
                        break;

                    case 4:
                        temp.you = "D";
                        break;
                    default:
                        break;
                }
                switch (item.rightOption) {
                    case 1:
                        temp.right = "A";
                        break;
                    case 2:
                        temp.right = "B";
                        break;
                    case 3:
                        temp.right = "C";
                        break;

                    case 4:
                        temp.right = "D";
                        break;
                    default:
                        break;
                }
                temp.score = item.score;
                this.displayError.push(temp);
            });
        },
        turnToMap(deptArr) {
            this.deptMap = {};
            deptArr.forEach(item => {
                this.deptMap[item.deptId] = item.deptName;
            });
        },
        getDeptTree() {
            return this.$http.get("/api/manage/getDeptNodeTree");
        },
        getDept() {
            return this.$http.get("/api/manage/getDeptNodes");
        },
        getStuInfo() {
            this.$http.get("/api/manage/getCurrentUser").then(res => {
                console.log("getCurrentUser", res.data);
                if (res.data.status == 200) {
                    let userInfo = res.data.data;
                    this.realName = userInfo.realName;
                    this.stuId = userInfo.userNum;
                    this.classs = this.deptMap[userInfo.classId];
                    this.grade = this.deptMap[userInfo.gradeId];
                    this.academy = this.deptMap[userInfo.academyId];
                    this.sex = userInfo.sex;
                    this.sexShow = true
                    this.userInfo = userInfo;
                }
            });
        },
        editInfo() {
            console.log("修改信息");
            this.modifyInfo = this.userInfo;
            this.modifyInfo.deptArr = [
                this.userInfo.academyId,
                this.userInfo.gradeId,
                this.userInfo.classId
            ];
            this.modifyModal = true;
        },
        confirmModify() {
            console.log("确认修改");
            if (this.modifyInfo.realName.length > 1) {
                if (
                    this.modifyInfo.deptArr[0] &&
                    this.modifyInfo.deptArr[1] &&
                    this.modifyInfo.deptArr[2]
                ) {
                    this.$http
                        .post("/api/manage/updateUserBasicInfo", {
                            realName: this.modifyInfo.realName,
                            userName: this.modifyInfo.userNum,
                            sex:this.modifyInfo.sex,
                            academyId: this.modifyInfo.deptArr[0],
                            gradeId: this.modifyInfo.deptArr[1],
                            classId: this.modifyInfo.deptArr[2]
                        })
                        .then(res => {
                            if (res.data.status == 200) {
                                this.$Message.success("修改成功");

                                setTimeout(() => {
                                    this.getStuInfo();
                                }, 10);
                                this.modifyModal = false;
                            } else {
                                this.$Message.error("修改失败");
                            }
                        });
                }
            }
        },
        // 修改密码
        editPwd() {
            console.log("修改当前学生密码");
            this.newPwd = "";
            this.reNewPwd = "";
            this.$Modal.confirm({
                render: h => {
                    return h("div", [
                        h("Input", {
                            props: {
                                type: "password",
                                value: this.newPwd,
                                autofocus: true,
                                placeholder: "输入新密码"
                            },
                            on: {
                                input: val => {
                                    this.newPwd = val;
                                }
                            }
                        }),
                        h("Input", {
                            props: {
                                type: "password",
                                value: this.reNewPwd,
                                autofocus: true,
                                placeholder: "确认新密码"
                            },
                            on: {
                                input: val => {
                                    this.reNewPwd = val;
                                }
                            },
                            style: {
                                marginTop: "10px"
                            }
                        })
                    ]);
                },
                onCancel: () => {
                    console.log("点了取消");
                    this.newPwd = "";
                },
                onOk: async () => {
                    console.log("点了确定");
                    if (
                        this.reNewPwd.length > 5 &&
                        this.reNewPwd.length < 20 &&
                        this.newPwd.length > 5 &&
                        this.newPwd.length < 20 &&
                        this.newPwd === this.reNewPwd
                    ) {
                        this.$http
                            .post("/api/manage/updatePassword", {
                                password: this.newPwd
                            })
                            .then(res => {
                                console.log("correct user pwd", res.data);
                                if (res.data.status == 200) {
                                    this.$Message.success({
                                        content: "修改成功"
                                    });
                                } else {
                                    this.$Message.error({
                                        contetn: "修改失败"
                                    });
                                }
                            });
                    } else {
                        this.$Message.error("修改失败,请检查输入的密码");
                    }
                }
            });
        }
    }
};

function deleteUn(treeArr) {
    let newTreeArr = treeArr.slice(0);
    let unIndex = newTreeArr.findIndex(item => {
        return item.deptType === 0;
    });
    newTreeArr.splice(unIndex, 1);
    return newTreeArr;
}

function traceDeptTree(treeArr) {
    let newTreeArr = treeArr.slice(0);
    treeArr.forEach(item => {
        item.label = item.deptName;
        item.value = item.deptId;
        if (item.children) {
            traceDeptTree(item.children);
        } else {
            delete item.children;
        }
    });

    return newTreeArr;
}
</script>

