import Vue from 'vue'
import Router from 'vue-router'

import Login from './pages/Login.vue'
import Register from './pages/Register.vue'
import Index from './pages/Index.vue'

import Hall from './subpages/Hall.vue'
import Practice from './subpages/Practice.vue'
import SelfCenter from './subpages/SelfCenter.vue'
import MockExam from './subpages/MockExam.vue'
import FormalExam from './subpages/FormalExam.vue'
import Lost from './pages/Lost.vue'
import Regulations from './subpages/Regulations.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/index',
      component: Index,
      children:[
        {
          path: '/index/',
          name: 'hall',
          component: Hall,
          meta:{
            requireAuth:true
          }
        },
        {
          path: '/index/practice',
          name: 'practice',
          component: Practice,
          meta:{
            requireAuth:true
          }
        },
        {
          path: '/index/selfcenter',
          name: 'selfcenter',
          component: SelfCenter,
          meta:{
            requireAuth:true
          }
        },
        {
          path: '/index/mockexam',
          name: 'mockexam',
          component: MockExam,
          meta:{
            requireAuth:true
          }
        },
        {
          path: '/index/formalexam',
          name: 'formalexam',
          component: FormalExam,
          meta:{
            requireAuth:true
          }
        },
        {
          path: '/index/regulations',
          name: 'regulations',
          component: Regulations,
          meta:{
            requireAuth:true
          }
        },
      ]
    },
    {
      path: '/',
      redirect:'/login'
    },
    {
      path:'*',
      name:'404',
      component:Lost
    }
    // {
    //   path: '/',
    //   name: 'hall',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ]
})
