<style scoped>
</style>
<template>
    <Collapse v-model="row" accordion>
        <Panel name="1">
            单选题
            <div class="content" slot="content">
                <Scroll v-if="scene === 'practice'" :on-reach-bottom="handleReachBottom">
                    <Qblock :status="blockMap[item]" @block-click="blockClick" v-for="(item, index) in displayArr" :qid="item" :key="item" :index="index+1"/>
                </Scroll>
                <Scroll v-else>
                    <Qblock :status="blockMap[item]" @block-click="blockClick" v-for="(item, index) in displayArr" :qid="item" :key="item" :index="index+1"/>
                </Scroll>
            </div>
        </Panel>
    </Collapse>
</template>

<script>
import Qblock from "./QuestionBlock";
import QCbus from "./QCbus.js";
export default {
  data() {
    return {
      row: "1",
      blockMap: {},
      displayArr: []
    };
  },
  created() {
    console.log("Qindex created", this.quesArr);
  },
  beforeCreate() {},
  beforeMount() {
    this.displayArr = this.quesArr;
    this.blockMap = {};
    this.quesArr.forEach(item => {
      this.$set(this.blockMap, item, "default");
    });

    QCbus.$on("answer-correct", arg => {
      let qid = arg.qid;
      this.blockMap[qid] = "correct";
    });
    QCbus.$on("answer-wrong", arg => {
      let qid = arg.qid;
      this.blockMap[qid] = "wrong";
    });

    QCbus.$on("answer-done", arg => {
      let qid = arg.qid;
      this.blockMap[qid] = "done";
    });

    QCbus.$on("answer-done-id", arg => {
      let qid = arg.qid;
      this.blockMap[qid] = "done";
    });

    QCbus.$on("answer-clear", arg => {
      let qid = arg.qid;
      this.blockMap[qid] = "default";
    });
  },
  mounted() {
    console.log("q index", this);
  },
  props: {
    quesArr: Array,
    scene: String // 使用场景  practice mock exam
  },
  watch:{
    quesArr:function(n,o){
      this.displayArr = n;
    this.displayArr.forEach(item => {
      if (!this.blockMap[item]) this.$set(this.blockMap, item, "default");
    });
    }
  },
  components: {
    Qblock
  },
  methods: {
    handleReachBottom() {
      switch (this.scene) {
        case "practice":
          return new Promise(resolve => {
            QCbus.$emit("load-more");
            console.log("触底反弹");
            // this.displayLen+=50;
            resolve();
          });
          break;
        default:
          console.log("考试类型，不加载");
          return null;
          break;
      }
    },
    blockClick(arg) {
      this.$emit("block-click", arg);
    }
  },
  beforeDestroy() {
    QCbus.$off("answer-correct");
    QCbus.$off("answer-wrong");
    QCbus.$off("answer-done");
    QCbus.$off("answer-done-id");
    QCbus.$off("answer-clear");
  }
};
</script>

