<style scoped>
.login-box {
    position: relative;
}

.login-card {
    width: 320px;
    position: absolute;
    right: 50px;
    top: 120px;
}

.card-title {
    margin-top: 10px;
    margin-bottom: 20px;
    text-align: center;
}

.form-box {
    margin: 0 auto;
    width: 80%;
}

.title-img {
    width: 500px;
    position: relative;
    top: 80px;
    left: 100px;
}
</style>
<template>
  <MainLayout>
    <div class="login-box">
      <img class="title-img" src="../assets/word.png" alt="">
      <div style="text-align:center;">
      </div>
      <Card class="login-card">
        <h4 class="card-title">考生登录</h4>
        <div class="form-box">
          <Form ref="formInline" :model="formInline" :rules="ruleInline">
            <FormItem prop="user">
              <Input type="text" v-model="formInline.user" placeholder="学号">
              <Icon type="ios-person-outline" slot="prepend"></Icon>
              </Input>
            </FormItem>
            <FormItem prop="password">
              <Input type="password" v-model="formInline.password" placeholder="密码">
              <Icon type="ios-lock-outline" slot="prepend"></Icon>
              </Input>
            </FormItem>
            <FormItem>
              <Button type="primary" @click="handleSubmit('formInline')">登录</Button>
              <Button style="margin-left:10px;" type="warning" @click="gotoRegister">注册</Button>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
  </MainLayout>
</template>

<script>
import MainLayout from "../Layout/Layout";
export default {
    data() {
        return {
            formInline: {
                user: "",
                password: ""
            },
            ruleInline: {
                user: [
                    {
                        required: true,
                        message: "请输入学号",
                        trigger: "blur"
                    }
                ],
                password: [
                    {
                        required: true,
                        message: "请输入密码",
                        trigger: "blur"
                    },
                    {
                        type: "string",
                        min: 6,
                        message: "密码至少6位",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    components: {
        MainLayout
    },
    methods: {
        handleSubmit(name) {
            this.$refs[name].validate(valid => {
                if (valid) {
                    this.$http
                        .post("/api/login", {
                            userName: this.formInline.user,
                            password: this.formInline.password
                        })
                        .then(res => {
                            console.log("login", res.data);
                            if (res.data.status == 200) {
                                let userInfo = res.data.data;
                                this.$store.dispatch('UserLogin',userInfo)
                                this.$Message.success("登陆成功");
                                this.$router.push("/index");
                            } else {
                                this.$Message.error("登陆失败");
                            }
                        });
                } else {
                    this.$Message.error("请查证您的输入");
                }
            });
        },
        gotoRegister() {
            this.$router.push("/register");
        }
    }
};
</script>