<style scoped>
.notice {
    margin: 20px;
    margin-bottom: 0px;
}
</style>
<template>
 <div>
   <Alert class="notice" type="warning" show-icon closable>正式考试过程请勿离开或刷新(F5)页面,否则有可能造成已答题目消失,或时间同步出错</Alert>
   <Row>
     <Row>
        <Col style="padding:20px 20px 0 20px;" span="6">
          <Timer :time="time" />
        </Col> 
        <Col style="padding:20px 20px 0 20px;" span="18">
          <StuInfo @hand-success="onHandSuccess" scene="exam" />
        </Col> 
     </Row>
     <QstCtl scene="exam" :quesArr="questionArr" v-if="questionArr.length"/>
      
   </Row>
   <Spin size="large" fix v-if="spinShow">考试初始化中,请稍后...</Spin>
 </div>
</template>

<script>
import QstCtl from "../components/QuestionControl";
import StuInfo from "../components/StudentInfo";
import Timer from "../components/ExamTimer";
import QCbus from "../components/QCbus.js";
export default {
    beforeRouteLeave(to, from, next) {
        if (this.canLeave) {
            next();
        } else {
            this.$Modal.confirm({
                title: "退出提醒",
                content: "确定离开考试，如已做完请主动交卷？",
            onOk:() => {
                    console.log('你点了ok')
                    next();
                },
                onCancel: () => {
                    next(false);
                },
            });
        }
    },
    beforeRouteEnter(to, from, next) {
        next();
    },
    data() {
        return {
            canLeave: false,
            questionArr: [],
            time: 0,
            spinShow: true
        };
    },
    async beforeMount() {
        this.spinShow = true;

        this.$exam.initControl("test", 0);
        try {
            let res = await this.$http.get("/api/formalExam/startformalExam");
            if (res.data.status == 200) {
                this.$http.get("/api/formalExam/getExamPaper").then(res => {
                    console.log(res.data);
                    if (res.data.status == 200) {
                        let data = res.data.data;
                        let startTime = data.startTime;
                        // let endTime = data.endTime;
                        let examTime = data.examTime;
                        let currentTime = data.currentTime;
                        let remainTime = examTime * 60 - (parseInt(currentTime) - parseInt(startTime));
                        let quesArr = this.parseExamList(data.examList);
                        this.time = remainTime;
                        this.questionArr = quesArr;
                        this.$exam.totalQues = quesArr.length;
                        this.$exam.setQuestionArr(quesArr);
                        this.spinShow = false;
                    }
                });
            }
        } catch (error) {
            console.log(error);
        }
    },
    components: {
        QstCtl,
        StuInfo,
        Timer
    },
    methods: {
        onHandSuccess() {
            this.canLeave = true;
        },
        getRecentTime() {
            return new Promise((resolve, reject) => {
                this.$http.get("/api/manage/currentTime").then(res => {
                    if (res.data.status == 200) {
                        console.log();
                        resolve(res.data.data);
                    } else {
                        reject("null");
                    }
                });
            });
        },
        parseExamList(examList) {
            let quesArr = [];
            examList.forEach(item => {
                quesArr.push(item.questionId);
                if (item.userOption > 0) {
                    this.$exam.markDone(item.questionId, item.userOption);
                    setTimeout(() => {
                        QCbus.$emit("answer-done-id", {
                            qid: item.questionId
                        });
                    }, 0);
                }
            });
            return quesArr;
        },
        getRecentAnswer() {
            console.log("getRecentAnswers");
            let answers = this.$exam.getAnswers();
            console.log("all answers", answers);
        }
    },
    beforeDestroy() {},
    destroyed() {}
};
</script>