<style scoped>

.card-box{
  
  width: 500px;
  background-color: rgba(255, 255, 255, 0.6);
  margin: 0 auto;
  border-radius: 10px;
  padding: 10px 5px;
  margin-top: 20px;
}

.form-box {
  width: 80%;
  margin: 0 auto;
}
.register-title {
  margin: 20px;
  text-align: center;
  font-size: 20px;
}
</style>
<template>
<MainLayout>
 <div class="register-box">
   <div class="card-box">

   
   <h3 class="register-title">考生注册</h3>
   <div class="form-box">
     <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <FormItem label="姓名" prop="name">
            <Input v-model="formValidate.name" placeholder="请输入姓名"></Input>
        </FormItem>
        <FormItem label="学号" prop="stuid">
            <Input v-model="formValidate.stuid" placeholder="请输入学号"></Input>
        </FormItem>
        
        <FormItem label="学院信息" prop="dept">
           <Cascader :data="cascaderData" v-model="formValidate.dept"></Cascader>
        </FormItem>

        <FormItem label="性别" prop="gender">
            <RadioGroup v-model="formValidate.gender">
                <Radio label="male">男</Radio>
                <Radio label="female">女</Radio>
            </RadioGroup>
        </FormItem>
        <FormItem label="密码" prop="password">
            <Input type="password" v-model="formValidate.password" placeholder="请输入你的密码"></Input>
        </FormItem>
        <FormItem label="确认密码" prop="repassword">
            <Input type="password" v-model="formValidate.repassword" placeholder="请确认你的密码"></Input>
        </FormItem>
        <FormItem style="text-align:center;">
            <Button type="success" @click="handleSubmit('formValidate')">注册</Button>
            <Button type="info" @click="handleReset('formValidate')" style="margin-left: 8px">重置</Button>
            <Button type="warning" to="/login" style="margin-left: 8px">返回</Button>
        </FormItem>
    </Form>
   </div>
  </div>
 </div>
</MainLayout>
</template>

<script>
import MainLayout from "../Layout/Layout";
export default {
  data() {
    return {
      cascaderData: [],
      formValidate: {
        name: "",
        stuid: "",
        password: "",
        repassword: "",
        dept: [],
        gender: ""
      },
      ruleValidate: {
        name: [
          {
            required: true,
            message: "必填",
            trigger: "blur"
          }
        ],
        stuid: [
          {
            required: true,
            message: "必填",
            trigger: "blur"
          },
          {
            type: "string",
            pattern:/^[0-9]*$/ig,
            message: "学号必须是数字",
            trigger: "blur"
          },{
            type: "string",
            min:3,
            max:20,
            message:"最少3位，最多20位",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: "必填",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: "密码至少6位",
            trigger: "blur"
          }
        ],
        repassword: [
          {
            required: true,
            message: "必填",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: "密码至少6位",
            trigger: "blur"
          }
        ],
        dept: [],
        gender: [{ required: true, message: "必选", trigger: "change" }]
      }
    };
  },
  components: {
    MainLayout
  },
  beforeMount() {
    this.getDeptTree();
  },
  methods: {
    getDeptTree() {
      this.$http.get("/api/manage/getDeptNodeTree").then(res => {
        console.log("dept info", res.data);
        if (res.data.status == 200) {
          let deptTree = res.data.data;
          console.log("dept tree", deptTree);
          let afterDelete = deleteUn(deptTree);
          this.cascaderData = traceDeptTree(afterDelete);
        } else {
          this.$Message.error({
            content: "网络错误"
          });
        }
      });
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          console.log(this.formValidate.gender)
          if (this.formValidate.dept.length>1) {
            if (this.formValidate.password === this.formValidate.repassword) {
              console.log(this.formValidate.gender)
                this.$http.post("/api/register", {
              userName: this.formValidate.stuid,
              realName: this.formValidate.name,
              password:this.formValidate.password,
              sex:this.formValidate.gender==='male'?0:1,
              academyId:this.formValidate.dept[0],
              gradeId:this.formValidate.dept[1],
              classId:this.formValidate.dept[2]
            })
            .then(res => {
              console.log("/api/register--------------->", res.data);
              if (res.data.status == 200) {
                this.$Message.success('注册成功，请返回登陆')
                this.$refs[name].resetFields();
              } else {
                this.$Message.error(res.data.msg)
              }
            });
            } else {
              this.$Message.error('两次密码不相同')
            }

          } else {
               this.$Message.error('请选择院系信息')
          }
            

          

        } else {
          this.$Message.error("请查证你的输入!");
        }
      });
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    }
  }
};

function deleteUn(treeArr) {
  let newTreeArr = treeArr.slice(0);
  let unIndex = newTreeArr.findIndex(item => {
    return item.deptType === 0;
  });
  newTreeArr.splice(unIndex, 1);
  return newTreeArr;
}

function traceDeptTree(treeArr) {
  let newTreeArr = treeArr.slice(0);
  treeArr.forEach(item => {
    item.label = item.deptName;
    item.value = item.deptId;
    if (item.children) {
      traceDeptTree(item.children);
    } else {
      delete item.children;
    }
  });

  return newTreeArr;
}
</script>

