import Vue from 'vue'
import Vuex from 'vuex'
import storage from './utils/storage'
Vue.use(Vuex)
const types={
  USER_LOGIN:'USER_LOGIN',
  USER_LOGINOUT:'USER_LOGINOUT',
  SAVE_DEPTTREE:'SAVE_DEPTTREE',
  SAVE_DEPTMAP:"SAVE_DEPTMAP"
}
export default new Vuex.Store({
  state: {
    userInfo:{
      userName:'',
      stuId:'',
      realName:'',
      userType:'',
      deptInfo:[],

    },
    deptTree:[],
    deptMap:{},
    isLogined:false
  },
  mutations: {
    [types.USER_LOGIN](state,userinfo){
      state.userInfo.userName = userinfo.userName;
      state.userInfo.realName = userinfo.realName;
      state.userInfo.stuId = userinfo.userNum;
      state.userInfo.userType = userinfo.userType;
      state.isLogined = true;
      state.deptInfo = [userinfo.academyId,userinfo.gradeId,userinfo.classId]
      storage.setSS('userName',userinfo.userName);
      storage.setSS('realName',userinfo.realName);
      storage.setSS('stuId',userinfo.userNum);
      storage.setSS('userType',userinfo.userType);
      storage.setSS('deptInfo',state.deptInfo)
      storage.setSS('isLogined','1');
    },
    [types.USER_LOGINOUT](state){
      state.userInfo = {
        userName:'',
        stuId:'',
        realName:'',
        userType:''
      },
      state.isLogined = false;
      storage.clearSS()
    },
    [types.SAVE_DEPTTREE](state,deptTree) {
      state.deptTree = deptTree
      storage.setSS('deptTree',JSON.stringify(deptTree))
    },
    [types.SAVE_DEPTMAP](state,deptMap) {
      state.deptMap = deptMap
      storage.setSS('deptMap',JSON.stringify(deptMap))
    },
  },
  actions: {
    UserLogin({ commit },data){
      commit(types.USER_LOGIN,data)
    },
    UserLoginOut({ commit }){
      commit(types.USER_LOGINOUT)
    },
    SaveDeptTree({ commit },data){
      commit(types.SAVE_DEPTTREE,data)
    },
    SaveDeptMap({ commit },data){
      commit(types.SAVE_DEPTMAP,data)
    }
  },
  getters:{
    getDeptTree: state=>{
      let deptTree = storage.getSS('deptTree')?JSON.parse(storage.getSS('deptTree')):state.deptTree
      return deptTree
    },
    getDeptMap: state=>{
      let deptMap = storage.getSS('deptMap')?JSON.parse(storage.getSS('deptMap')):state.deptMap
      return deptMap
    },
    getDeptInfo:state=>{
      let deptInfo = storage.getSS('deptInfo')?storage.getSS('deptInfo'):state.deptInfo
      return deptInfo
    },
    realName:state=>{
      return storage.getSS('realName')?storage.getSS('realName'):state.userInfo.realName
    },
    stuId:state=>{
      return storage.getSS('stuId')?storage.getSS('stuId'):state.userInfo.stuId
    },
  }
})
