<style scoped>
.content-box {
    margin: 20px 30px;
}
.regu-title {
    text-align: center;
    margin: 10px auto;
}
.regu-box {
    background-color: white;
    border-radius: 10px;
}
.regu-content {
    margin: 5px 20px;
}
.select-item {
    cursor: pointer;
}
</style>


<template>
    <div class="content-box">
         <Row>
             <Col span="5">
                <Scroll height="500">
                    <Card @click.native="itemClick(item.regulationId)" class="select-item" dis-hover v-for="(item, index) in list" :key="index" style="margin: 5px 0">
                        {{ item.title }}
                    </Card>
                </Scroll>
             </Col>
             <Col push="1" span="18">
                 <Scroll class="regu-box" height="500">
                     <div class="regu-title">
                         <h2>{{displayTitle}}</h2>
                     </div>
                    <div class="regu-content" v-html="displayHtml"></div>
                </Scroll>
                <Spin size="large" fix v-if="spinShow"></Spin>
             </Col>
         </Row>
    </div>
</template>


<script>
export default {
  data() {
    return {
        spinShow:false,
      list: [],
      displayTitle:'',
      displayHtml:''
    };
  },
  beforeMount(){
      this.getAllList().then(res=>{
          if (res.data.status==200) {
              let arr = res.data.data;
              this.buildList(arr)
              if (arr.length>0) {
                this.getOneById(this.list[0].regulationId)
              }
              
          } else {
              this.$Message.error('网络错误')
          }
      })
      
  },
  methods: {
      getAllList(){
          return this.$http.get(`/api/regulation/findAllRegulationList`)
      },
      getOneById(id){
          this.spinShow = true
          this.$http.get(`/api/regulation/findRegulationById?regulationId=${id}`).then(res=>{
              if (res.data.status == 200) {
                  let title = res.data.data.regulationTitle;
                  let content = res.data.data.regulationText;
                  this.renderRight(title,content)
                  this.spinShow = false
              } else {
                  this.spinShow = false
              }
          })
      },
      buildList(arr){
          this.list = []
          let index = 0
            arr.forEach(item=>{
                let temp={}
                temp.title = item.regulationTitle
                temp.regulationId = item.regulationId
                temp.index = index++
                this.list.push(temp)
            })
      },
      itemClick(regulationId){
        console.log(regulationId)
        this.getOneById(regulationId)
      },
      renderRight(title,html){
          this.displayTitle = title;
          this.displayHtml = html;
      }
  }
};
</script>

